@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');


/*===================================Variable CSS===============================================*/
:root{
  --header-height:3rem;
  /*==============================Colors========================*/
  /*==============================color-mode:hsl(hue,saturation,lightness==============================*/
  --hue:0;
  --sat:0%;
  --title-color:hsl(var(--hue),var(--sat),20%);
  --title-color-dark:hsl(var(--hue),var(--sat),0%);
  --text-color:hsl(var(--hue),var(--sat),46%);
  --body-color:hsl(var(--hue),var(--sat),98%);
  --container-color:#fff;
  /*--------------------------fontcolor and typography---------------------------------------*/
  --body-font:"Poppins", sans-sarif;
  --big-font-size:3.5rem;
  --h1-font-size:2.25rem;
  --h2-font-size:1.5rem;
  --h3-font-size:1.25rem;
  --normal-font-size:1rem;
  --small-font-size:0.875rem;
  --smaller-font-size:0.813rem;
  --tiny-font-size:0.625rem;
  /*------------------------------------Font weight---------------------------------------------------*/
--font-normal:400;
--font-medium:500;
--font-semi-bold:600;
/*----------------------------------------Margin-bottom------------------------------------------------------------*/
--mb-0-25:0.25rem;
--mb-0-5:0.5rem;
--mb-0-75:0.75rem;
--mb-1:1rem;
--mb-1-5:1.5rem;
--mb-2-5:2.5rem;
--mb-3:3rem;
/*---------------------------------------Z=index--------------------------------------------------------*/
--z-tooltip:10;
--z-fixed:100;
--z-modal:1000;
}
/*============================================Responsive topography==================================================*/
@media screen and (max-width:992px){
  :root{
    --big-font-size:2.75rem;
    --h1-font-size:1.5rem;
    --h2-font-size:1.25rem;
    --h3-font-size:1rem;
    --h1-font-size:1.5rem;
    --normal-font-size:0.938rem;
    --small-font-size:0.813rem;
    --smaller-font-size:0.75rem;
  }
}
/*------------------------------------Base-----------------------------*/
*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
}
body,button,input,textarea{
  font-family: var(--body-font);
  font-size:var(--normal-font-size);

}
body{
  background-color: var(--body-color);
  color: var(--text-color);
}
h1,h2,h3{
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}
ul{
  list-style: none;
}
a{
  text-decoration: none;
}
button{
  cursor: pointer;
  border: none;
  outline: none;
}
img{
  max-width: 100%;
  height: auto;
}
/*------------------------------------Reusable css classess-----------------------------*/
.section{
  padding: 6rem 0 2rem;

}
.section_title{
  font-size: var(--h1-font-size);
  color: var(--title-color);

}
.section_subtitle{
  display: block;
  font-size: var(--small-font-size);
  margin-bottom: 4rem;
}
.section_title,.section_subtitle{
  text-align: center;
}
/*========================================LAYOUT==============================================*/
.container{
  max-width: 968px;
  margin-left: auto;
  margin-right: auto;
}
.grid{
  display: grid;
  gap: 1.5rem;
}

/*========================================BUTTONS==============================================*/
.button{
  display: inline-block;
  background-color: var(--title-color);
  color: var(--container-color);
  padding: 1.25rem 2rem;
  border-radius:1rem;
  font-weight: var(--font-medium);

}
.button:hover{
  background-color:var(--title-color-dark);

}
.button_icon{
  margin-left: var(--mb-0-5);
}
.button--flex{
  display: inline-flex;
  align-items: center;

}







